export default "<template>\r\n    <div class=\"home\">\r\n        <raw-loader :mytxt=\"require('!raw-loader!./TestLogin.vue').default\"></raw-loader>\r\n        <h1 class=\"border-bottom\">Test Login</h1>\r\n        <b-row>\r\n            <b-col class=\"border-right\" md=\"3\">\r\n                <div v-if=\"isLogged() == 0\">\r\n                    Username :<input v-model=\"acc.username\" /><br>\r\n                    Password :<input v-model=\"acc.password\" /><br>\r\n                    <button @click=\"onTestLogin\">Login</button>\r\n                </div>\r\n                <div v-else>\r\n                    User info :<br>\r\n                    <button class=\"mr-1\" @click=\"onLogout\">Logout</button>\r\n                </div>\r\n                <div>\r\n                    <h3 class=\"border-bottom mt-1\">Test area</h3>\r\n                    <button class=\"mr-1\" @click=\"GetUserInfo\">Get User Info</button>\r\n                    <button class=\"mr-1\" @click=\"TestXemBan\">Test xem Ban</button>\r\n                    <button class=\"mr-1\" @click=\"TestThemBan\">Test Them Ban</button>\r\n                    <button class=\"mr-1\" @click=\"TestXoaBan\">Test Xoa Ban</button>\r\n                    <button class=\"mr-1\" @click=\"TestSuaBan\">Test Sua Ban</button>\r\n                    <button class=\"mr-1\" @click=\"GetQuyenAll\">Get Quyen all</button>\r\n                    <button class=\"mr-1\" @click=\"TestK1Policy\">TestK1Policy</button>\r\n                    \r\n                    <hr>\r\n                    <button class=\"mr-1\" @click=\"TestThemBanHang\">Test Them Ban Hang</button>\r\n\r\n                </div>\r\n            </b-col>\r\n            <b-col md=\"6\">\r\n                {{ msg }}\r\n                <role-level-com></role-level-com>\r\n                <h3>List permission</h3>\r\n                <b-table :items=\"ds\" :fields=\"fields\">\r\n                    <template v-slot:cell()=\"{ value, item, field: { key } }\">\r\n                        <template v-if=\" key=='claimName' \">\r\n                            {{ value }}\r\n                        </template>\r\n                        <template v-if=\" key=='list' \">\r\n                            <span v-for=\"(v1,k1) in value\">\r\n                                {{ k1 }}\r\n                                <input disabled=\"disabled\" class=\"mr-2\" v-if = \"v1==true\" checked type=\"checkbox\" >\r\n                                <input disabled=\"disabled\" class=\"mr-2\" v-else type=\"checkbox\" >\r\n                            </span>\r\n                        </template>\r\n                    </template>    \r\n                </b-table>\r\n            </b-col>\r\n            <b-col md=\"3\">\r\n                <pre class=\"large-font border\">{{ userInfo }}</pre>\r\n            </b-col>\r\n        </b-row>\r\n        <div>\r\n        </div>\r\n    </div>\r\n</template>\r\n<script>\r\nimport fetchClient from '@/fetchClient';\r\nimport TumLum from '@/TumLum';\r\nimport RoleLevelCom from \"@/views/NguoiDung/RoleLevelCom.vue\"\r\nexport default {\r\n    components: {\r\n        RoleLevelCom\r\n    },\r\n    data() {\r\n        return {\r\n            acc: {\r\n                username: 'admin',\r\n                password: '1234'\r\n            },\r\n            msg: {},\r\n            userInfo: {},\r\n            ds:[],\r\n            fields:[\r\n                { key: 'claimName', \r\n                    sortable: true, thClass: 'text-center', tdClass: 'align-middle', thStyle: { width: \"20%\" }},\r\n                { key: 'list', \r\n                    sortable: true, thClass: 'text-center', tdClass: 'align-middle', thStyle: { width: \"80%\" }},\r\n            ],\r\n            token: '',\r\n        }\r\n    },\r\n    mounted() {\r\n        let token = (localStorage.getItem(\"token\")) ? localStorage.getItem(\"token\") : '';\r\n        this.token = token;\r\n        if (token != '') {\r\n            this.GetUserInfo();\r\n            this.GetQuyenAll();\r\n        }\r\n    },\r\n    methods: {\r\n        isLogged() {\r\n            let kq = 1;\r\n            let token = (localStorage.getItem(\"token\")) ? localStorage.getItem(\"token\") : '';\r\n            if (token == '') kq = 0;\r\n            return kq;\r\n        },\r\n        onLogout() {\r\n            localStorage.removeItem(\"token\");\r\n            location.reload();\r\n        },\r\n        onTestLogin() {\r\n            this.Login(this.acc);\r\n        },\r\n        async Login(acc) {\r\n            return await fetchClient.post(`/api/NguoiDung4/Login`, acc)\r\n                .then(res => {\r\n                    alert(`Đăng nhập thành công.`);\r\n                    let token = res.data.kq.Token;\r\n                    localStorage.setItem(\"token\", token);\r\n                    location.reload();\r\n                })\r\n                .catch(err => {\r\n                    TumLum.XuLyErr(err);\r\n                })\r\n        },\r\n        async GetUserInfo() {\r\n            return await fetchClient.get(`/api/NguoiDung4/GetUserInfo`)\r\n                .then(res => {\r\n                    this.userInfo = res.data;\r\n                })\r\n                .catch(err => {\r\n                    TumLum.XuLyErr(err);\r\n                })\r\n        },\r\n        async TestXemBan() {\r\n            return await fetchClient.get(`/api/NguoiDung4/TestXemBan`)\r\n                .then(res => {\r\n                    this.msg = res.data;\r\n                })\r\n                .catch(err => {\r\n                    TumLum.XuLyErr(err);\r\n                })\r\n        },\r\n        async TestThemBan() {\r\n            return await fetchClient.get(`/api/NguoiDung4/TestThemBan`)\r\n                .then(res => {\r\n                    this.msg = res.data;\r\n                })\r\n                .catch(err => {\r\n                    TumLum.XuLyErr(err);\r\n                })\r\n        },\r\n        async TestXoaBan() {\r\n            return await fetchClient.get(`/api/NguoiDung4/TestXoaBan`)\r\n                .then(res => {\r\n                    this.msg = res.data;\r\n                })\r\n                .catch(err => {\r\n                    TumLum.XuLyErr(err);\r\n                })\r\n        },\r\n        async TestSuaBan() {\r\n            return await fetchClient.get(`/api/NguoiDung4/TestSuaBan`)\r\n                .then(res => {\r\n                    this.msg = res.data;\r\n                })\r\n                .catch(err => {\r\n                    TumLum.XuLyErr(err);\r\n                })\r\n        },\r\n        async TestThemBanHang() {\r\n            return await fetchClient.get(`/api/NguoiDung4/TestThemBanHang`)\r\n                .then(res => {\r\n                    //this.msg = res.data;\r\n                    alert(JSON.stringify(res));\r\n                })\r\n                .catch(err => {\r\n                    TumLum.XuLyErr(err);\r\n                })\r\n        },\r\n        async TestK1Policy() {\r\n            return await fetchClient.get(`/api/NguoiDung4/TestK1Policy`)\r\n                .then(res => {\r\n                    //this.msg = res.data;\r\n                    alert(JSON.stringify(res));\r\n                })\r\n                .catch(err => {\r\n                    TumLum.XuLyErr(err);\r\n                })\r\n        },\r\n\r\n\r\n\r\n\r\n        async GetQuyenAll() {\r\n            return await fetchClient.get(`/api/NguoiDung4/GetQuyenAll`)\r\n                .then(res => {\r\n                    //alert(JSON.stringify(res));\r\n                    /*{\"data\":[{\r\n                        \"claimName\":\"Ban\",\r\n                        \"list\":{\"Xem\":false,\"The                    \r\n                    */\r\n                    //this.quyens = res.data;\r\n                    this.ds=res.data;\r\n                })\r\n                .catch((err) => {\r\n                    TumLum.XuLyErr(err);\r\n                });\r\n        },\r\n\r\n\r\n    }\r\n}\r\n</script>\r\n<style scoped>\r\n.large-font {\r\n    font-size: 10.5px;\r\n    height: 300px;\r\n    overflow: auto;\r\n}\r\n\r\n/*pre {\r\n  height: 100px;\r\n  overflow: auto;\r\n}*/\r\n</style>\r\n  ";