<template>
    <div class="home">
        <h1>List role level</h1>
        <b-table small 
            :items="items" 
            :fields="[]">
            <!-- <template v-slot:cell()="{ value, item, field: { key } }">
                <template v-if=" key=='Key' ">
                    {{ value }}
                </template>
                <template v-if=" key=='Value' ">
                    {{ value }}
                </template>
            </template>     -->
        </b-table>
    </div>
</template>

<script>
import fetchClient from '@/fetchClient'
import TumLum from '@/TumLum'
export default {
    components: {
    },
    data(){
        return{
            items:[],
        }
    },
    mounted(){
        this.GetListRoleLevel();
    },
    methods: {
        async GetListRoleLevel(){
            return await fetchClient.get(`/api/NguoiDung4/GetListRoleLevel`)
            .then(res=>{
                //alert(JSON.stringify(res));
                //{"data":[{"Key":"Admin","Value":9},{"Key":"ChuQuan","Value":8},{"Key":"KeT
                //this.items=res.data;
                let ar=[];
                let ds=res.data;
                let r={}
                ds.forEach(x => {
                    r[x.Key]=x.Value;                    
                });
                ar.push(r);
                this.items=ar;
                //alert(JSON.stringify(ar));
            }).catch(err=>{
                TumLum.XuLyErr(err);
            })
        }
    }
}
</script>
  